<template>
    <h1>GitHub</h1>
    <input type="text" placeholder="Repository Name:" v-model="repo" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Owner:" v-model="owner" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Issue Title:" v-model="title" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Content:" v-model="body"/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getGithub">Confirm</button>
</template>

<script>    
export default {
    name: "GithubREA",
    data() {
        return {
            repo: null,
            owner : null,
            title: null,
            body: null,
            tmpREA: []
        }
    },
    methods: {
        getGithub() {
            if (this.repo != null && this.title != null && this.owner != null && this.body != null) {
                let R = {
                    "repo": this.repo,
                    "owner": this.owner,
                    "title": this.title,
                    "body": this.body
                }
                let REA = {
                    "content": JSON.stringify(R),
                    "id": "636277770bf8e24d4f8155ee",
                    "name": "Github",
                    "description": "Create an issue on a GitHub repository"
                }
                if (localStorage.reaction) {
                    this.tmpREA = JSON.parse(localStorage.reaction)
                }
                this.tmpREA.push(REA)
                localStorage.reaction = JSON.stringify(this.tmpREA)
                this.$router.push({ name: 'Recap' });
            } else {
                alert('Not all information filled.')
            }
        },
    }
}
</script>

